<template>
  <div class="redlist_box">
    <div class="redlist_nav">
      <div class="redlist_nav_btn" @click="goBack"><i class="el-icon-back"></i></div>
      <div class="redlist_nav_title">创建YAS账号</div>
      <div class="redlist_nav_text">
        <router-link to="/"><i class="el-icon-s-home"></i></router-link>
      </div>
    </div>
    <div class="redlist_content">
      <div class="redlist_header">
        <div>YAS数量</div>
        <div>状态</div>
      </div>
      <div class="redlist_item_box">
        <template v-if="data.length == 0"><div class="redlist_no_data">暂无数据</div></template>
        <template v-else>
          <div v-for="(item,index) of data" class="redlist_item" :key="index" @click="goDetail">
              <div class="item_left">
                <span class="num">{{item.amount}}</span>
                <span class="luck">普</span>
                <span class="count-down"><i class="el-icon-time"></i>{{item.expire | time(4)}}</span>
              </div>
              <div class="item_right">
                <span>0/{{item.limit}}</span>
              </div>
          </div>
        </template>
      </div>
    </div>
  </div>

</template>

<script>
import Enu from 'enujs';
export default {
    data () {
      return {
        data: [
            {
                "id":"15438247056751496",
                "type":1,
                "limit":1,
                "sender":"sunshine1234",
                "pubkey":"YAS8MYdgFY9PYkoZKq96ERhnoe8qAggPexbXX8WE8PepEB2n2sUyd",
                "amount":"0.1000 YAS",
                "memo":"恭喜发财，大吉大利",
