                "pubkey":"YAS8MYdgFY9PYkoZKq96ERhnoe8qAggPexbXX8WE8PepEB2n2sUyd",
                "amount":"0.1000 YAS",
                "memo":"恭喜发财，大吉大利",
                "expire":1543911168,
                "log":[

                ]
            },
            {
                "id":"15438248205210710",
                "type":1,
                "limit":1,
                "sender":"sunshine1234",
                "pubkey":"YAS8MYdgFY9PYkoZKq96ERhnoe8qAggPexbXX8WE8PepEB2n2sUyd",
                "amount":"1.0000 YAS",
                "memo":"恭喜发财，大吉大利",
                "expire":1543911223,
                "log":[

                ]
            }
        ]
      }
    },
    created(){

    },

    methods:{
      goBack(){
        window.history.go(-1);
      },
      goDetail(){
          this.$router.push({name: 'detail'});
      }
    },
    computed: {

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @rem: round(1/75*2,6)*1rem;
    .redlist_box{
      width: 100%;
      height: 100%;
      .redlist_nav{
          width: 100%;
          height: 44*@rem;
          line-height: 44*@rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 15*@rem;
          background: #ce2344;
          box-sizing: border-box;
          .redlist_nav_btn{
            width: 30*@rem;
            height: 44*@rem;
            display: flex;
            align-items: center;
            i{
              color: #fff;
              font-size: 25*@rem;
            }
          }
          .redlist_nav_title{
              font-size: 12*@rem;
              line-height: 44*@rem;
              color: #fff;
          }
          .redlist_nav_text{
              a{
                width: 30*@rem;
                height: 44*@rem;
                display: flex;
                align-items: center;
                text-align: right;
                i{
                  color: #fff;
                  font-size: 23*@rem;
                }
              }

          }
      }
      .redlist_content{
        .redlist_header{
          padding: 0 15*@rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 30*@rem;
          background-color: #f8f8f8;
          font-size: 14*@rem;
        }
        .redlist_item_box{
          width: 100%;
          height: 100%;
          background: #fff;
          .redlist_no_data{
            height: 50*@rem;
            line-height: 50*@rem;
            text-align: center;
            font-size: 12*@rem;
            color: #a69987;
          }
          .redlist_item{
              height: 72*@rem;
              border-bottom: 1*@rem solid #f9f9f9;
              padding: 0 15*@rem;
              display: flex;
              justify-content: space-between;
              align-items: center;
              .item_left{
                display: flex;
                align-items: center;
                span{
                  margin-right: 6*@rem;
                  font-size: 12*@rem;
                }
                .num{
                  font-size: 18*@rem;
                  color: #5d4220;
                }
                .luck{
                  color: #288efb;
                  background-color: rgba(40,142,251,.1);
                  padding: 2*@rem 3*@rem;
                }
                .count-down{
                  color: #ff9200;
                  background-color: #fff5e8;
                  padding: 2*@rem 3*@rem;
                  font-weight: 500;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                }
              }
              .item_right{
                  max-width: 66px;
                  text-align: right;
                  font-size: 12*@rem;
                  color: #5d4220;
              }
          }
        }
      }
    }
</style>



